@import "../mixins";
@import "../config";

.info-compact {
  .header {
    padding: $app-base-padding;
    border-bottom: 1px solid lighten($color-gray-lighten, 30%);

    @include box-sizing();

    .links {
      margin-top: $app-base-padding;

      a {
        margin-left: $app-base-padding / 2;
        margin-right: $app-base-padding / 2;

        color: lighten($color-blue, 15%);
        transition: 0.1s color;

        &:hover {
          color: $color-blue-darken;
        }

        &:first-child {
          margin-left: 0;
        }

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  .table {
    tr td, tr th {
      padding: $app-base-padding;
    }
  }
}
