@import "mixins";
@import "config";

.form {
  .form-group {
    width: 100%;

    display: flex;
    align-items: center;

    margin-top: $app-form-top-offset;
    margin-bottom: $app-form-bottom-offset;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.label-force-top {
      align-items: flex-start;

      .label {
        padding-top: $app-base-padding;
      }
    }

    & > .label {
      width: 25%;
      min-width: $app-form-label-min-width;
      flex-grow: 0;
      flex-shrink: 0;

      padding: $app-form-label-padding;
      text-align: right;
      font-weight: bold;

      @include user-select();
      @include box-sizing();
    }

    &.has-offset {
      margin-top: $app-form-label-padding;

      &.force {
        margin-top: $app-form-label-padding !important;;
      }
    }

    & > .input-wrapper {
      width: 100%;

      @include box-sizing();

      & > .input {
        border: none;
        background-color: $color-white;
        padding: $app-form-input-padding;

        box-shadow: 0 0 10px -2px rgba(0, 0, 0, .2);
        border-radius: 5px;

        transition: 0.1s box-shadow;

        @include box-sizing();

        &[type="text"],
        &[type="password"],
        &[type="email"],
        &[type="date"],
        &[type="number"],
        & {
          display: block;
          width: 100%;
          outline: none;
          color: $color-gray;

          &:focus {
            box-shadow: 0 0 20px -4px rgba(0, 0, 0, .45);

            &::-moz-placeholder {
              color: darken($color-gray-lighten, 15%);
            }

            &::-webkit-input-placeholder {
              color: darken($color-gray-lighten, 15%);
            }

            &:-ms-input-placeholder {
              color: darken($color-gray-lighten, 15%);
            }
          }

          &::-moz-placeholder {
            color: $color-gray-lighten;
          }

          &::-webkit-input-placeholder {
            color: $color-gray-lighten;
          }

          &:-ms-input-placeholder {
            color: $color-gray-lighten;
          }
        }
      }

      &.radio-group {
        & > label {
          display: flex;
          width: 100%;

          margin-top:    $app-form-top-offset    / 2;
          margin-bottom: $app-form-bottom-offset / 2;

          cursor: pointer;
          outline: none;

          @include user-select();

          &:first-child {
            margin-top: 0;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }

        & > label > input[type="radio"] {
          display: none;

          position: relative;

          & + .input-wrapper {
            display: inline-block;
            top:  0;
            left: 0;
            width:  $app-form-radiobutton-size;
            height: $app-form-radiobutton-size;
            margin-right: $app-base-padding;
            border-radius: 50%;

            position: relative;

            &::before {
              content: "";
              display: inline-block;
              width:  100%;
              height: 100%;
              position: absolute;
              top: 0;
              left: 0;

              border-radius: 50%;
              border: 2px solid $color-gray-lighten;

              @include box-sizing();
            }
          }

          &:checked + .input-wrapper {
            background-color: $color-white;
            position: relative;

            &::after {
              content: "";
              position: absolute;
              top:  4px;
              left: 4px;
              width:  $app-form-radiobutton-circle-size;
              height: $app-form-radiobutton-circle-size;
              background-color: $color-gray-lighten;
              border-radius: 50%;

              box-shadow: 0 0 5px rgba(0, 0, 0, .1);
            }
          }
        }
      }

      &.checkbox-group {
        & > label {
          display: flex;
          width: 100%;

          margin-top:    $app-form-top-offset    / 2;
          margin-bottom: $app-form-bottom-offset / 2;

          cursor: pointer;
          outline: none;

          @include user-select();

          &:first-child {
            margin-top: 0;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }

        & > label > input[type="checkbox"] {
          display: none;

          position: relative;

          & + .input-wrapper {
            display: inline-block;
            top:  0;
            left: 0;
            width:  $app-form-radiobutton-size;
            height: $app-form-radiobutton-size;
            margin-right: $app-base-padding;

            position: relative;

            &::before {
              content: "";
              display: inline-block;
              width:  100%;
              height: 100%;
              position: absolute;
              top: 0;
              left: 0;

              border: 2px solid $color-gray-lighten;

              @include box-sizing();
            }
          }

          &:checked + .input-wrapper {
            background-color: $color-white;
            position: relative;

            &::after {
              content: "check";
              font-family: "Material Icons", sans-serif;
              position: absolute;
              top:  1px;
              left: 1px;
              color: darken($color-gray-lighten, 15%);
              font-size: 16px;

              box-shadow: 0 0 5px rgba(0, 0, 0, .1);
            }
          }
        }
      }

    }
  }
}
