@import "../mixins";
@import "../config";

@media screen and(max-width: $app-responsive-mobile-max-width) {
  header.app-header {
    #global-search {
      margin-left: $app-header-search-field-left-offset;

      &.loading {
        &::after {
          right: 24px;
        }
      }

      input[type="text"] {
        //margin-left: $app-header-search-field-left-offset;

        //&.active {
        //  width: $app-header-search-field-responsive-mobile-width;
        //}
        //
        //&:focus {
        //  width: $app-header-search-field-responsive-mobile-width;
        //}
      }
    }

    a {
      &.menu {
        display: block;
      }
    }
  }
}

@media screen and (max-width: $app-responsive-hide-sidebar) {
  header.app-header {
    a {
      &.menu {
        display: block;
      }
    }
  }
}

@media screen and (max-width: $app-responsive-mobile-max-width) {
  header.app-header {
    a {
      &.username {
        display: none;
      }

      &.bell {
        &[data-notification] {
          &::after {
            font-size: $app-header-bell-mobile-font-size;
          }
        }
      }
    }

    #global-search {
      padding-right: $app-base-padding * 1.5;

      @include box-sizing();

      input[type="text"] {
        //width: $app-header-search-field-responsive-mobile-width;
        //
        //&:focus {
        //  width: $app-header-search-field-responsive-mobile-width;
        //}
      }
    }
  }
}
