@import "mixins";
@import "config";

.grid {
  display: flex;

  &::after {
    content: "";
    display: block;
    overflow: hidden;
    clear: both;
  }

  &.grid-static {
    @for $i from 1 to $app-columns {
      .col-#{$i} {
        width: 100% / $app-columns * $i;

        @include box-sizing();
      }
    }

  }

  .col {
    width: 100%;
  }

  & > .pull-right {
    margin-right: 0;
    text-align: right;
  }

}
