@import "mixins";
@import "config";

.table {
  width: 100%;
  border: 0;
  border-spacing: 0;

  &.table-striped {
    tbody {
      tr:nth-child(2n) {
        td {
          background-color: rgba(0, 0, 0, .02);
        }
      }
    }
  }

  &.table-small {
    tbody tr td, thead tr th {
      padding: $app-base-padding;

      &.has-input {
        input, .input {
          padding: $app-base-padding;
        }
      }

      &.has-list {
        ul {
          li {
            padding: $app-base-padding;
          }
        }
      }
    }
  }

  thead {
    tr {
      th {
        font-weight: bold;
        text-align: left;
        background-color: rgba(0, 0, 0, .03);

        padding: $app-base-padding * 2;

        @include user-select();
        @include box-sizing();
      }
    }
  }

  tbody {
    tr {
      td {
        padding: $app-base-padding * 2;

        @include box-sizing()
      }
    }
  }

  tbody tr td, thead tr th {
    font-size: $app-base-font-size;

    &.pull-right {
      text-align: right;
      float: none;
    }

    &.has-input {
      padding: 0;
      vertical-align: top;

      input, .input {
        display: block;
        width: 100%;
        border: none;
        background-color: transparent;
        padding: $app-base-padding * 2;
        line-height: 14px;
        font-size: 14px;
      }
    }

    &.has-list {
      padding: 0;
      vertical-align: top;
    }

    &.no-paddings {
      padding: 0;
    }

    &.no-vertical-paddings {
      padding-top: 0;
      padding-bottom: 0;
    }

    &.small-right-padding {
      padding-right: $app-base-padding;
    }

    &.vertical-aligned {
      vertical-align: middle;
    }

    ul {
      li {
        display: block;
        padding: $app-base-padding;

        @include box-sizing();

        &:first-child {
          padding-top: $app-base-padding * 2;
        }

        &:last-child {
          padding-bottom: $app-base-padding * 2;
        }
      }
    }
  }

}
