@import "../mixins";
@import "../config";

#notifications-container {
  position: fixed;
  right: $app-base-padding * 4;
  bottom: $app-base-padding * 2;

  .notification {
    margin-top: $app-base-padding;
    margin-bottom: $app-base-padding;

    box-shadow: 0 0 15px rgba(0, 0, 0, .15);
    background-color: $color-white;
    width: $app-notification-width;
    border-radius: 3px;
    position: relative;

    min-height: 36px;

    @include box-sizing();

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &.notification-danger, &.notification-success {
      .notification-close {
        &:hover {
          i {
            color: white;
          }
        }

        i {
          color: rgba(255, 255, 255, .8);
        }
      }
    }

    &.notification-danger {
      background-color: $color-red;
      color: $color-white;

      ::selection {
        background-color: $color-white;
        color: $color-red-darken;
      }

      ::-moz-selection {
        background-color: $color-red-darken;
        color: $color-white;
      }

      .notification-content {
        .notification-actions {
          .btn {
            background-color: lighten($color-red-darken, 5%);
            transition: 0.1s background;
            padding: $app-base-padding * 1.25;

            &:hover {
              background-color: darken($color-red-darken, 5%);
            }
          }
        }
      }
    }

    &.notification-success {
      background-color: $color-green;
      color: $color-white;

      ::selection {
        background-color: $color-white;
        color: $color-green-darken;
      }

      ::-moz-selection {
        background-color: $color-green-darken;
        color: $color-white;
      }

      .notification-content {
        .notification-actions {
          .btn {
            background-color: lighten($color-green-darken, 5%);
            transition: 0.1s background;
            padding: $app-base-padding * 1.25;

            &:hover {
              background-color: darken($color-green-darken, 5%);
            }
          }
        }
      }

    }

    .notification-content {
      width: 100%;
      padding: $app-base-padding * 1.5;
      padding-right: 36px;

      @include box-sizing();

      .notification-actions {
        margin-top: $app-base-padding * 1.5;

        &::after {
          content: "";
          display: block;
          clear: both;
          overflow: hidden;
        }

        .btn {
          float: left;
        }
      }
    }

    .notification-close {
      position: absolute;
      right: 0;
      top: 0;

      @include user-select();
      @include box-sizing();

      &:hover {
        i {
          color: $color-gray-darken;
        }
      }

      i {
        font-size: 16px;
        font-weight:bold;
        color: $color-gray;
        transition: 0.1s color;
        cursor: pointer;

        display: inline-block;

        padding: $app-base-padding;
      }
    }

  }
}
