@import "config";
@import "mixins";

.btn {
  padding: $app-button-padding;
  text-decoration: none;

  background-color: lighten($color-gray-darken, 15%);
  color: $color-white;
  border-radius: 3px;
  border: none;

  outline: none;

  @include user-select();
  @include box-sizing();

  &:hover {
    color: $color-white;
    background-color: $color-gray-darken;
  }

  &.btn-large {
    padding: $app-button-large-padding;
  }

  &.btn-small {
    padding: $app-button-small-padding;
  }

  &.btn-success {
    background-color: $color-green;

    &:hover {
      background-color: $color-green-darken;
    }
  }

  &.btn-danger {
    background-color: $color-red;

    &:hover {
      background-color: $color-red-darken;
    }
  }

  &.like-btn {
    background-color: transparent;
    color: $color-gray-darken;
    border-radius: 0;

    position: relative;

    &:hover {
      background-color: rgba(0, 0, 0, .03);
    }

    &.no-hover {
      &:hover {
        background-color: transparent;
      }
    }
  }
}

.btn-group {
  &::after {
    content: "";
    display: block;
    clear: both;
    overflow: hidden;
  }

  .btn {
    border-radius: 0;
    float: left;

    &:first-child {
      border-top-left-radius: 3px;
      border-bottom-left-radius: 3px;
    }

    &:last-child {
      border-top-right-radius: 3px;
      border-bottom-right-radius: 3px;
    }
  }
}
