@import "mixins";
@import "config";

.mi-icon {
  padding: $app-base-padding;
  display: inline-block;
  border-radius: 50%;

  font-family: "Material Icons", sans-serif;
  text-decoration: none;
  outline:none;

  font-size: 14px;

  @include box-sizing();

  &:hover {
    background-color: rgba(0, 0, 0, .05);
  }

  &.use-shadow {
    &:hover {
      background-color: white;
      box-shadow: 0 0 10px rgba(0, 0, 0, .15);
    }
  }
}
