@import "mixins";
@import "config";

.tabs {
  width: 100%;

  .tabs-navigation {
    width: 100%;
    display: flex;

    align-items: center;

    padding: 0 $app-tabs-navigation-horizontal-padding;
    box-shadow: 0 0 10px rgba(0, 0, 0, .1);

    @include user-select();
    @include box-sizing();

    &.normal-right-padding {
      padding-right: 10px;
    }

    &::after {
      content: "";
      display: block;
      clear: both;
      overflow: hidden;
    }

    & > a, & > .item {
      width: auto;

      display: inline-block;
      padding: $app-tabs-navigation-item-padding 0;
      padding-bottom: $app-tabs-navigation-item-padding - 3;
      outline: none;
      text-decoration: none;
      border-bottom: 3px solid transparent;

      margin-left: $app-tabs-navigation-item-padding;
      margin-right: $app-tabs-navigation-item-padding;

      @include user-select();
      @include box-sizing();

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }

      &.pull-right {
        margin-left: auto;
      }

      &.active {
        border-bottom-color: $color-blue-darken;
        color: $color-blue-darken;
      }
    }

    .dropdown {
      position: relative;

      &.active {
        .title {
          box-shadow: 0 0 15px rgba(0, 0, 0, .08);
          color: $color-gray-darken;
        }

        ul {
          display: inline-block;
          position: absolute;
          top:   30px;
          right: 0;
        }
      }

      .title {
        display: inline-block;
        padding: 5px 10px;
        padding-right: 30px;
        border: 1px solid lighten($color-gray, 25%);
        border-radius: 3px;
        position: relative;
        cursor: pointer;

        &::after {
          content: "keyboard_arrow_down";
          font-family: "Material Icons", sans-serif;
          font-size: 24px;
          position: absolute;
          top: 1px;
          right: 4px;
        }

        &:hover {
          box-shadow: 0 0 15px rgba(0, 0, 0, .08);
          color: $color-gray-darken;
        }

        input[type="checkbox"] {
          display: none;
        }
      }
      
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        display: none;
        background-color: $color-white;
        border-radius: 3px;
        border: 1px solid lighten($color-gray-lighten, 30%);
        white-space: nowrap;

        li {
          & > a, & > span {
            display: block;
            width: 100%;

            padding: 10px 15px;
            text-decoration: none;

            outline: none;

            @include user-select();
            @include box-sizing();
          }
        }
      }

      &.pull-right {
        margin-left: auto;
      }
    }
  }

  .tabs-contents {
    & > div {
      padding-top: $app-tabs-content-item-padding;
      padding-bottom: $app-tabs-content-item-padding;

      @include box-sizing();

      &.no-paddings {
        padding: 0;
      }

      &.no-right-padding {
        padding-right: 0;
      }

      &.no-left-padding {
        padding-left: 0;
      }

      &:not(.visible) {
        display: none;
      }
    }
  }

}
