@import "../mixins";
@import "../config";

@media screen and (max-width: $app-responsive-hide-sidebar) {
  .app-container {
    display: block;

    .sidebar-wrapper {
      height: auto;
      position: absolute;
      top:  5px;
      left: 5px;
      max-width: $app-responsive-sidebar-width;
      padding-right: 0;
      border-radius: 3px;

      display: none;

      z-index: 9999;

      &.active {
        display: block;
      }

      .sidebar {
        border-radius: 3px;

        ul {
          li  {
            &:first-child {
              a {
                border-top-left-radius: 3px;
                border-top-right-radius: 3px;
              }
            }
            &:last-child {
              a {
                border-bottom-left-radius: 3px;
                border-bottom-right-radius: 3px;
              }
            }

            a {
              &[data-list-header] {
                &::after {
                  content: "keyboard_arrow_right";
                  right: 7px;
                  top:   3px;
                  bottom: auto;
                }
              }

              &.active {
                background-color: rgba(0, 0, 0, .03);

                &::before {
                  background-color: transparent;
                }
              }
            }
          }
        }
      }
    }

    .app-content {
      padding-left: 0;
    }
  }
}

@media screen and (min-width: $app-responsive-mobile-max-width) and (max-width: $app-responsive-max-width) {
  .app-container {
    .sidebar-wrapper {
      width: 200px;
    }
  }
}
