@import "../mixins";
@import "../config";

.ghost-btn {
  display: inline-block;

  text-transform: uppercase;
  padding: 12px;
  text-decoration: none;
  transition: 0.1s color, 0.1s background;

  @include user-select();
  @include box-sizing();

  &.ghost-btn-primary {
    font-weight: bold;
    border-radius: 3px;

    &:hover {
      background-color: lighten($color-gray, 42%);
    }
  }
}
