@import "mixins";
@import "config";

header.app-header {
  padding: $app-header-padding;

  @include box-sizing();

  .grid {
    .col-main {
      width: 100%;
      display: flex;
    }

    .col-user-info {
      flex-shrink: 0;
      flex-grow: 0;
      margin-left: auto;
    }
  }

  .pull-right {
    a {
      float: right;
    }
  }

  #global-search {
    margin-right: $app-base-padding;
    position: relative;
    width: 100%;

    &.loading {
      &::after {
        content: "sync";
        font-family: "Material Icons", sans-serif;

        position: absolute;
        font-size: 20px;

        right: 12px;
        top:   12px;

        color: $color-blue;

        animation: rotating 1s linear infinite;

        @-webkit-keyframes rotating {
          from {
            transform: rotate(0deg);
          }
          to {
            transform: rotate(360deg);
          }
        }
      }

      input[type="text"] {
        padding-right: 38px;
      }
    }

    input[type="text"] {
      display: inline-block;
      //border: 1px solid lighten($color-gray-lighten, 15%);
      box-shadow: 0 0 15px -2px rgba(0, 0, 0, .15);
      border: none;

      padding: $app-header-search-field-padding;
      font-size: 14px;
      line-height: 19px;
      border-radius: 3px;
      //width: $app-header-search-field-min-width;
      width: 100%;

      transition: 0.1s width, 0.1s box-shadow;
      outline: none;
      position: relative;

      &:focus, &.active {
        border-color: $color-gray;
        width: $app-header-search-field-max-width;
        box-shadow: 0 0 25px -2px rgba(0, 0, 0, .25);
      }
    }

    .search-results {
      position: absolute;
      top: 50px;
      left: 0;
      width: 100%;
      background-color: $color-white;

      border-radius: 3px;
      overflow: hidden;
      box-shadow: 0 0 15px -5px rgba(0, 0, 0, .2);

      .result-group {
        .result-group-header {
          font-weight: bold;

          padding: 12px;

          @include user-select();
          @include box-sizing();
        }

        .result-item {
          padding: 12px;

          @include user-select();
          @include box-sizing();

          &:hover {
            background-color: rgba(0, 0, 0, .02);
          }

          a {
            color: lighten($color-gray-darken, 15%);
            text-decoration: underline;

            transition: 0.15s color;

            &:hover {
              color: $color-blue;
            }
          }
        }
      }
    }

  }

  a {
    text-decoration: none;
    outline: none;

    &.menu {
      padding: $app-header-menu-padding;
      float: left;

      outline:none;
      display: none;

      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      @include box-sizing();
    }

    &.username {
      padding: $app-header-username-padding;
    }

    &.bell {
      padding: 10px;

      outline: none;

      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;

      @include box-sizing();

      &[data-notification] {
        position: relative;

        &::after {
          content: attr(data-notification);
          position: absolute;
          bottom: 8px;
          right:  4px;
          font-size: $app-header-bell-font-size;
          background-color: $color-green-darken;
          color: $color-white;

          padding: 2px 5px;
          border-radius: 10px;
          font-weight: bold;

          @include box-sizing();
        }
      }
    }
  }

}
