@import "../../mixins";
@import "../../config";

.cards {
  .card {
    width: 100%;

    .card-header, .card-content, .card-footer {
      padding: $app-base-padding * 1.5;
    }

    .card-content {
      &.no-paddings {
        padding: 0;
      }

      &.has-bottom-offset {
        padding-bottom: $app-base-padding;
      }

      .diff {
        .diff-header, .diff-from, .diff-to {
          padding: $app-base-padding * 1.5;
        }
      }
    }
  }
}
