@import "../mixins";
@import "../config";

@media screen and (max-width: $app-responsive-mobile-max-width) {

  .tabs {
    .tabs-navigation {
      display: block;
      padding: 0;
      border-radius: 3px;

      &.normal-right-padding {
        padding: 0;
      }

      a {
        display: block;
        width: 100%;
        padding: $app-base-padding * 1.5;
        margin: 0;
        border: none;
      }
    }

    .tabs-contents {
      margin-top: $app-base-padding * 1.5;

      & > div {
        padding: 0;
        margin: 0;
      }
    }
  }

}
