@import "mixins";
@import "config";

.message {
  padding: $app-base-padding;
  border-radius: 3px;
  background-color: $color-blue;
  color: white;
  font-weight: bold;

  @include user-select();
  @include box-sizing();

  &.message-danger {
    background-color: $color-red;

    .btn {
      color: $color-red-darken;

      &:hover {
        background-color: $color-red-darken;
        color: $color-white;
      }
    }
  }

  &.message-info {
    background-color: $color-orange;

    .btn {
      color: $color-orange-darken;

      &:hover {
        background-color: $color-orange-darken;
        color: $color-white;
      }
    }
  }

  &.message-ghost {
    border: 1px solid $color-blue;
    color: $color-blue;
    background-color: transparent;
    
    &.message-danger {
      color: $color-orange;
      border-color: $color-red;
    }
    
    &.message-info {
      color: $color-orange;
      border-color: $color-orange;
    }
  }

  .btn {
    background-color: $color-white;
    color: $color-blue-darken;
    border-radius: 3px;

    &:hover {
      background-color: $color-blue-darken;
    }
  }

  p {
    margin-top: $app-base-padding;
    margin-bottom: $app-base-padding;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  .message-footer {
    margin-top: $app-base-padding;

    &::after {
      content: "";
      display: block;
      clear: both;
      overflow: hidden;
    }

    * {
      float: left;
    }
  }

}
