@import "../mixins";
@import "../config";

.popup-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow-y: scroll;

  background-color: rgba(0, 0, 0, .2);

  .popup {
    width: $app-popup-width;
    background-color: $color-white;

    margin: 50px auto;
    padding: $app-base-padding;
    border-radius: 3px;

    box-shadow: 0 0 25px -2px rgba(0, 0, 0, .3);

    @include user-select();
    @include box-sizing();

    &.popup-no-content-paddings {
      padding: 0;

      .popup-header, .popup-footer {
        padding: $app-base-padding;

        @include box-sizing();
      }
    }

    &.popup-loader {
      width: 30px + $app-base-padding * 2;
      padding: $app-base-padding;
    }

    .popup-header {
      display: flex;
      align-items: flex-start;
      width: 100%;
      margin-bottom: $app-base-padding;

      &.popup-header-large-bottom-offset {
        margin-bottom: $app-base-padding * 2;
      }

      .popup-title {
        width: 100%;

        .title {
          font-size: 16px;
          color: $color-gray-darken;
        }

        .subtitle {
          font-size: 12px;
          margin-top: $app-base-padding;
          color: $color-gray;
        }
      }

      .popup-close {
        width: auto;
        max-width: 50px;
        flex-grow: 0;
        flex-shrink: 0;

        i {
          color: lighten($color-red, 15%);
          cursor: pointer;
          padding: 5px;
          margin-top: -5px;
          margin-right: -5px;
          transition: 0.1s color;

          @include box-sizing();

          &:hover {
            color: $color-red-darken;
          }
        }
      }
    }

    .popup-content {
      /* popup content */

      /* popup form */
      .form {
        .form-group {
          display: block;

          &:first-child {
            .label {
              padding-top: 0;
            }
          }

          &:not(.label-force-top) {
            margin-top: 0;
            margin-bottom: 0;
          }

          .label {
            width: 100%;
            text-align: left;
            padding-left: 0;
            padding-right: 0;
          }
        }
      }

    }

    .popup-footer {
      margin-top: $app-base-padding;
      display: flex;

      &::after {
        content: "";
        display: block;
        clear: both;
        overflow: hidden;
      }

      &.popup-header-large-top-offset {
        margin-top: $app-base-padding * 2;
      }

      .pull-right {
        float: none;
        margin-left: auto;

        &::after {
          content: "";
          display: block;
          clear: both;
          overflow: hidden;
        }
      }

      .ghost-btn {
        float: left;
      }
    }
  }
}
