@import "config";

body {
  font-family: 'Noto Sans', sans-serif;
  font-weight: normal;
  font-size: 14px;
  color: $color-gray;

  padding: 0;
  margin: 0;

  background-color: $color-white;
}

a {
  color: $color-gray;

  &:hover {
    color: $color-gray-darken;
  }
}

.pull-right {
  float: right;
}

.bold {
  font-weight: bold;
}

.clear {
  clear: both;
  overflow: hidden;
}

.size-testable-area {
  position: absolute;
  left: -99999px;
  top: -99999px;
}

.offset-top {
  margin-top: $app-base-padding;

  &.force {
    margin-top:$app-base-padding !important;
  }
}

.offset-bottom {
  margin-bottom: $app-base-padding;
}

.offset-left {
  margin-left: $app-base-padding;
}

.offset-right {
  margin-right: $app-base-padding;
}

.offset-vertical {
  margin-top: $app-base-padding;
  margin-bottom: $app-base-padding;
}

.offset-horizontal {
  margin-left: $app-base-padding;
  margin-right: $app-base-padding;
}

.no-top-padding {
  padding-top: 0 !important;
}

.no-bottom-padding {
  padding-bottom: 0 !important;
}

.no-left-padding {
  padding-left: 0 !important;
}

.no-right-padding {
  padding-right: 0 !important;
}

.hide {
  display: none !important;
}

.inline-list {
  * {
    float: left;
  }

  &::after {
    content: "";
    display: block;
    overflow: hidden;
    clear: both;
  }

  &.pull-right {
    float: right;
  }
}

/* selection */
::selection {
  background-color: lighten($color-gray-lighten, 25%);
  color: darken($color-gray-darken, 15%);
}

::-moz-selection {
  background: lighten($color-gray-lighten, 25%);
  color: darken($color-gray-darken, 15%);
}
