@import "mixins";
@import "config";

.pagination {

  list-style: none;
  margin: $app-base-padding 0 0;
  padding: 0;

  &::after {
    content: "";
    display: block;
    clear: both;
    overflow: hidden;
  }

  li {
    margin: 0;
    padding: 0;

    float: left;
    display: block;

    &.page-item {
      color: $color-gray;
    }

    &.active {
      span {
        background-color: rgba(0, 0, 0, .05);
        color: $color-gray-darken;
      }
    }

    span, a {
      padding: $app-pagination-padding;
      display: block;

      outline: none;

      @include user-select();
      @include box-sizing();

      &:not(span) {
        &:hover {
          background-color: rgba(0, 0, 0, .05);
          color: $color-gray-darken;
        }
      }
    }

  }

}
