@import "mixins";
@import "config";

.app-container {
  display: flex;
  width: 100%;

  .sidebar-wrapper {
    flex-grow: 0;
    flex-shrink: 0;
    width: $app-sidebar-width;

    padding-right: $app-sidebar-right-padding;
  }

  .app-content {
    padding-left: $app-sidebar-right-padding;
    min-height: 100vh;

    width: 100%;
  }

}
