@import "../mixins";
@import "../config";

.calendar-container {
  display: flex;
  align-items: flex-start;

  .calendar {
    width: $app-calendar-width;
    flex-grow:0;
    flex-shrink:0;

    padding-right: $app-calendar-right-offset;

    @include user-select();
    @include box-sizing();

    .calendar-header, .calendar-content, .calendar-footer {
      width: 100%;
    }

    .calendar-header {
      display: flex;
      align-items: center;
      width: 100%;
      margin-bottom: $app-base-padding;

      .prev-month, .next-month {
        width: 25%;
        text-align: center;
      }

      .current-month {
        font-size: 24px;
        color: $color-blue-darken;
        width: 50%;
        text-align: center;
      }
    }

    .calendar-content {
      table {
        width: 100%;

        thead {
          tr {
            th {
              font-weight: bold;
              color: $color-blue-darken;
            }
          }
        }

        tr th {
          text-align: center;
          padding: $app-calendar-column-padding;
        }

        tr td {
          &.disabled {
            color: lighten($color-gray, 20%);
          }

          &.active {
            color: $color-blue;
            font-weight: bold;
            cursor: pointer;
          }

          &.highlight {
            background-color: $color-blue;
            color: $color-white;
          }
        }

        tr td .day-wrapper {
          text-align: center;
          padding: $app-calendar-column-padding;
          float: left;
          display: block;
          width: 100%;

          @include box-sizing();
        }
      }
    }

    .calendar-footer {
      padding: $app-base-padding;

      @include box-sizing();

      .btn-group {
        display: flex;
        
        .btn {
          text-align: center;
        }
      }

      .btn {
        width: 100%;
        cursor: pointer;
      }
    }

  }

  .calendar-day {
    padding-left: $app-base-padding;
    width: 100%;
    max-width: 300px;
    float: left;

    @include box-sizing();

    .header {
      display: flex;
      align-items: center;

      h3 {
        display: inline-block;
        width: auto;
      }

      .create-item {
        margin-left: $app-base-padding;

        .btn {
          float: left;
        }

        &::after {
          content: "";
          display: block;
          clear: both;
          overflow: hidden;
        }
      }
    }

    .list {
      margin-top: $app-base-padding;
      margin-bottom: $app-base-padding;
      margin-left: -$app-base-padding;

      li {
        border-radius: 3px;
        transition: 0.1s box-shadow;

        padding: $app-base-padding;

        @include box-sizing();

        &:hover {
          box-shadow: 0 0 25px rgba(0, 0, 0, .15);
        }

        .actions {
          &::after {
            content: "";
            clear: both;
            overflow: hidden;
            display: block;
          }

          .btn {
            float: left;
          }
        }

        p {
          margin-top:    7px;
          margin-bottom: 7px;

          &:first-child {
            margin-top: 0;
          }

          &:last-child {
            margin-bottom: 0;
          }

          &.name {
            font-size: 16px;
          }

        }
      }
    }
  }

}
