@import "../mixins";
@import "../config";

@media screen and (max-width: $app-responsive-mobile-max-width) {

  .table {
    thead tr th, tbody tr td {
      display: block;
      float: left;
      width: 100%;
    }

    tbody tr td:last-child {
      border-bottom: 1px solid rgba(0, 0, 0, .1);
    }

    tbody tr:last-child td:last-child {
      border-bottom: none;
    }
  }

}
