@import "config";
@import "mixins";

.custom-select {
  &.select {
    .select-header {
      color: $color-gray;
      cursor: pointer;
      position: relative;

      transition: 0.1s color, 0.1s box-shadow;

      padding: 14px;
      padding-top: 11px;
      padding-bottom: 11px;
      padding-right: 14px * 2 + 10px;
      border-radius: 3px;

      line-height: 17px;
      font-size: 14px;

      box-shadow: 0 0 10px -2px rgba(0,0,0,0.2);

      @include box-sizing();
      @include user-select();

      &:hover {
        color: $color-blue-darken;
        box-shadow: 0 0 20px -4px rgba(0,0,0,0.45);

        &::after {
          //border-bottom-color: $color-blue-darken;
          border-color: $color-gray transparent transparent transparent
        }
      }

      &::after {
        content: "";
        position: absolute;

        right: 12px;
        top:   15px;

        width: 0;
        height: 0;
        border-style: solid;
        border-width: 8px 5.5px 0 5.5px;
        border-color: lighten($color-gray, 15%) transparent transparent transparent;

        transition: 0.1s border;

        //left: 0;
        //bottom: 0;
        //width: 100%;
        //border-bottom: 1px dashed $color-blue;
        //transition: 0.1s border;
      }
    }
  }
}
// .popup-wrapper .popup .popup-header.popup-header-large-bottom-offset
.popup-wrapper {
  .popup {
    &.custom-select-popup {
      .popup-header {
        &.popup-header-large-bottom-offset {
          margin-bottom: 0;
        }
      }

      .popup-footer {
        margin-top: 0;
      }

      .popup-content {
        &.custom-select-wrapper {
          form {
            &.select-search {
              input {
                display: block;
                width: 100%;
                border: none;
                padding: $app-base-padding;

                @include box-sizing();
              }
            }
          }

          .select-option {
            padding: 15px 40px 15px 12px;
            position: relative;

            @include box-sizing();

            &:hover {
              background-color: lighten($color-gray-lighten, 35%);
              cursor: pointer;
            }

            &.active {
              &::after {
                content: "check";
                font-family: "Material Icons", sans-serif;
                position: absolute;
                top: 12px;
                right: 12px;
                font-size: 20px;
                color: $color-blue;
              }
            }

            .item-description {
              font-size: 12px;
              position: relative;
              margin-top: $app-base-padding;
            }
          }
        }
      }
    }
  }
}
