@import "../mixins";
@import "../config";

.card {
  width: $app-card-width;
  background-color: $color-white;
  box-shadow: 0 0 15px rgba(0, 0, 0, .15);
  border-radius: 3px;

  @include box-sizing();

  .card-header, .card-content, .card-footer {
    padding: $app-base-padding;

    @include box-sizing();
  }

  .card-header {

    &[data-icon] {
      padding-left: $app-base-padding * 2 + 44px;
      position: relative;

      &::before {
        content: attr(data-icon);
        font-family: "Material Icons", sans-serif;
        font-size: 44px;

        position: absolute;
        top: $app-base-padding;
        left: $app-base-padding;
      }
    }

    .title {
      font-size: 20px;

      &.title-normal-font-size {
        font-size: 16px;
      }
    }

    .subtitle {
      margin-top: $app-base-padding;
    }
  }

  .card-content {
    /* empty */

    &.no-paddings {
      padding: 0;
    }

    &.has-bottom-offset {
      padding-bottom: $app-base-padding;

      @include box-sizing();
    }

    p {
      margin-top: $app-base-padding;
      margin-bottom: $app-base-padding;

      &:first-child {
        margin-top: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    .diff {
      &.active {
        .diff-header {
          color: $color-gray-darken;
        }

        .diff-from, .diff-to {
          display: block;
        }
      }

      .diff-header {
        padding: $app-base-padding;
        cursor: pointer;

        @include box-sizing();

        &:hover {
          background-color: lighten($color-gray-lighten, 32%);
        }
      }

      .diff-from, .diff-to {
        padding: $app-base-padding;
        display: none;

        @include box-sizing();
      }

      .diff-from {
        background-color: lighten($diff-color-red, 5%);
        border-top: 1px solid $diff-color-red;
      }

      .diff-to {
        background-color: lighten($diff-color-green, 5%);
        border-bottom: 1px solid $diff-color-green;
      }
    }
  }

  .card-footer {
    text-align: right;

    & > * {
      float: right;
    }

    &.pull-right {
      float: none;

      &::after {
        content: "";
        display: block;
        clear: both;
        overflow: hidden;
      }
    }
  }

}

.cards {
  .card {
    margin-top: $app-base-padding;
    margin-bottom: $app-base-padding;

    &:first-child {
      margin-top: 0;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }
}
