@import "mixins";
@import "config";

.sidebar-wrapper {
  height: 100vh;
  background-color: white;

  .sidebar {
    height: 100%;
    box-shadow: 0 0 25px -15px lighten($color-gray-lighten, 15%);

    &::after {
      content: "";
      display: block;
      clear: both;
      overflow: hidden;
    }

    ul {
      margin: 0;
      padding: 0;
      list-style: none;

      ul {
        display: none;
      }

      li {
        @include box-sizing();

        &.active {
          background-color: rgba(0, 0, 0, .02);

          & > ul {
            display: block;
          }

          & > a {
            &[data-list-header] {
              background-color: rgba(0, 0, 0, .03);
              color: $color-gray-darken;

              &::after {
                transform: rotate(180deg);
              }
            }
          }

        }

        a {
          display: block;
          padding: $app-sidebar-item-padding;
          padding-left: $app-sidebar-item-padding + $app-sidebar-active-indicator-width;
          text-decoration: none;
          outline: none;
          position: relative;

          -webkit-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;

          &[data-list-header] {
            padding-right: $app-sidebar-item-large-padding;

            &::after {
              content: 'keyboard_arrow_down';
              font-family: "Material Icons", sans-serif;

              position: absolute;
              right: 10px;
              top:   2px;

              font-size: 30px;
              transition: 0.15s transform;
            }
          }

          &.active {
            color: darken($color-gray-darken, 15%);
            box-shadow: 0 -5px 5px -5px rgba(0, 0, 0, .05),
                        0  5px 5px -5px rgba(0, 0, 0, .05);

            &::before {
              content: "";
              display: block;
              width: $app-sidebar-active-indicator-width;
              height: 100%;
              position: absolute;
              top: 0;
              left: 0;

              background-color: lighten($color-gray-darken, 30%);
            }
          }
        }
      }
    }
  }
}
