@import "../mixins";
@import "../config";

@media screen and(max-width: $app-responsive-hide-sidebar) {

  .sidebar-wrapper {
    .sidebar {
      box-shadow: 0 0 25px rgba(0, 0, 0, .25);

      /*
      ale•••••••••••••••
      alexey.rudkowskij0
      alex.rudkovskiy0
       */

      ul {
        li {
          &.active {
            background: none;
            
            ul {
              display: none;
            }

            & > a {
              &[data-list-header] {
                &::after {
                  transform: none;
                }
              }
            }
          }

          a {
            &.active {
              box-shadow: none;
            }
          }
        }
      }
    }
  }

}
